@import url("https://fonts.googleapis.com/css?family=Montserrat&display=swap");

body {
  padding-top: 5vh;
  background-size: cover;
  background-image: url("../Images/party.jpg") !important;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

#title {
  max-width: 350px;
  margin-top: -40px;
  margin-bottom: 10px;
  margin-left: 45px;
}

h2 {
  width: 400px;
  text-align: left;
  font-size: larger;
  font-weight: lighter;
  color: #3d4254;
  padding-left: 0.5em;
  padding-bottom: 0.25em;
  margin-left: -55px;
  margin-right: 70px;
  margin-bottom: 5px;
  margin-top: 5px;
  border-bottom: 1px solid #3d4254;
  font-family: "Montserrat", sans-serif;
}

#plus {
  max-width: 50px;
  margin-bottom: -30px;
  margin-top: 40px;
  margin-left: -415px;
  margin-right: 415px;
  -webkit-box-shadow: 5x -5px rgba(0, 0, 0, 0.6);
  -o-box-shadow: 5px -5px rgba(0, 0, 0, 0.6);
  border-radius: 100px;
}

span {
  position: relative;
  color: #4389a2;
  text-align: left;
  margin-right: 600px;
  margin-left: -30px;
  color: #3d4254;
  text-transform: capitalize;
  margin-top: 100px;
  font-weight: lighter;
  padding-top: 10px;
  font-family: "Montserrat", sans-serif;
}

form {
  vertical-align: middle;
  min-width: 200px;
  max-width: 300px;

  padding: 0.5em 4em;
  margin: auto;
  border: 1px solid #121a26;
  border-radius: 10px;
  align-items: center;
  background-image: linear-gradient(#121a26, #20232ff2, #121a26);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

input {
  border: 1px solid #3d4254;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1em;
  text-align: center;
  font-size: large;
  color: white;
  background-color: transparent;
  margin-left: 1em;
  margin-right: 2em;
  vertical-align: middle;
  outline: 0;
  padding-right: 0.5em;
  padding-left: 0.5em;
  border-radius: 20px;
  min-width: 200px;
  max-width: 300px;
  font-family: sans-serif;
}

#nameInput {
  background-color: transparent;
  border: 0px;
  background: -webkit-linear-gradient(#5c258d, #4389a2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
  text-transform: capitalize;
  font-weight: bold !important;
  font-size: xx-large;
  border-bottom: 1px solid white;
  max-width: 600px;
  overflow: hidden;
  margin-left: -50px;
  text-align: center;
  margin-bottom: 5px;
  padding-bottom: 0.25em;
  border-radius: 0px;
  border-bottom: 1px solid #3d4254;
  text-shadow: 2px 2px 4px #0000001a;
  font-family: "Montserrat", sans-serif;
}

::placeholder {
  font-family: "Montserrat", sans-serif;
  font-weight: lighter;
  font-size: medium;
  font-style: italic;
  margin-bottom: 200px;
}

#dateInput {
  margin-left: 20em;
  margin-right: 2em;
  padding-right: 0.5em;
  padding-left: 0.5em;
}

#locationInput,
#timeInput {
  margin-top: 0.2em;
  margin-bottom: 0.5em;
  text-align: center;
  font-size: large;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-right: 0.2em;
  padding-left: 0.2em;
  outline: 0;
  width: 650px;
  border: 1px solid #3d4254;
  margin-left: -10px;
  font-family: "Montserrat", sans-serif;
  border-radius: 20px;
  background-color: transparent;
}

#descriptionInput {
  font-family: "Montserrat", sans-serif;
  justify-content: center;
  display: flex;
  font-size: large;
  display: flex;
  align-items: center;
  padding: 0.5em 2em;
  outline: 0;
  margin-bottom: 1em;
  margin-top: 5px;
  min-width: 200px;
  max-width: 250px;
  min-height: 25px;
  background-color: transparent;
  color: white;
  border-radius: 20px;
  border: 1px solid #3d4254;
}

button {
  font-family: "Montserrat", sans-serif;
  text-align: center;
  vertical-align: middle;
  text-align: center;
  font-size: large;
  font-weight: bolder;
  padding: 0.5em;
  margin-bottom: 1em;
  margin-left: -5px;
  border-radius: 20px;
  width: 200px;
  color: white !important;
  border-radius: 20px !important;
  text-transform: uppercase;
  text-decoration: none;
  background-image: linear-gradient(
    to bottom right,
    #5c258d,
    #4389a2
  ) !important;
  border: 0.125px solid transparent;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  display: inline-block;
  transition: all 0.4s ease 0s;
}

button:hover {
  color: #ffffff !important;
  background-image: linear-gradient(
    to bottom right,
    #bd6be3,
    #e95c57
  ) !important;
  border: 5px solid #c567c9;
  transition: all 0.4s ease 0s;
}

#smiles {
  max-width: 130px;
  margin-top: 20px;
}
